* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: arial;
}

.container {
    margin: 20px auto 0;
    width: 80%;
    max-width: 960px;
    min-width: 680px;
    background-color: rgb(248, 238, 233);
}

.settings {
    position: relative;
    width: 100%;
    height: 50px;
    background-color: #FFD9C4;
}

.settings a{
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    margin-right: 30px;
    color: black;
    text-decoration: none;
    padding: 5px 15px;
    letter-spacing: 2px;    
    border-radius: 10px;
    font-size: 0.9rem;
    transition: .3s;
    background-color: rgb(197, 161, 143);
    border: 1px solid brown;
}

.settings a:hover{
    background-color: rgb(116, 78, 59);
    color: white;
}

.list {
    margin: 5px 4%;
    padding-bottom: 5px;
}


